.container {
    margin-top: 88px;
    
    .title {
        color: #ffffff;
        font-family: 'GilroyBoldItalic';
        font-size: 88px;
        text-align: center;
    }

    .info {
        color: #C5C5C5;
        font-family: 'GilroyRegular';
        font-size: 32px;
        margin-top: 33px;
        text-align: center;
    }
}

@media (max-width: 425px) {
    .container {

        .title {
            font-size: 36px;
            margin-top: 74px;
        }

        .info {
            font-size: 16px;
        }
    }
}